/* General Styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
}

/* Header Styling */
h1 {
  margin: 0;
  padding: 1rem 0;
  text-align: center;
  font-size: 3rem;
  font-weight: bold;
}

/* Grid Layout for Week */
.grid-cols-7 {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 8px;
  width: 100%;
  max-width: 100vw;
  margin: 0 auto;
  padding: 0 8px;
  margin-bottom: 2rem; /* Extra space between header and rings */
}

/* Ring Container */
.ring-container {
  width: 100%;
  max-width: 70px;
  margin: 0 auto;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Responsive for Small Screens */
@media (max-width: 600px) {
  .grid-cols-7 {
      grid-template-columns: repeat(4, 1fr);
      gap: 8px;
  }

  .w-24 {
      width: 60px;
      height: 60px;
  }

  .text-xs {
      font-size: 0.65rem;
  }
}

/* Margin Utilities */
.mb-2 {
  margin-bottom: 0.5rem;
}

.mb-6 {
  margin-bottom: 1.5rem;
}

/* Size Utilities */
.w-24 {
  width: 70px;
  height: 70px;
}

/* Positioning */
.absolute {
  position: absolute;
}

.relative {
  position: relative;
}

.inset-0 {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

/* Font Styles */
.font-bold {
  font-weight: bold;
}

.text-xs {
  font-size: 0.75rem;
}

/* Meal Button Styles */
.meal-button {
  padding: 0.5rem 1.5rem;
  border-radius: 0.5rem;
  color: #fff;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s ease;
  width: 86%; /* Reduce width by about 7% */
  margin-bottom: 1rem;
}

/* Background Colors */
.meal-button.bg-green-500 {
  background-color: #34C759;
}

.meal-button.bg-yellow-500 {
  background-color: #FFCC00;
}

.meal-button.bg-red-500 {
  background-color: #FF3B30;
}

.meal-button:hover {
  opacity: 0.8;
}

/* Button Container */
.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem; /* Extra space to move buttons down */
}
